.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.debugger {
  height: 100vh;
  position: fixed;
  width: 100%; }
  .debugger > div {
    height: inherit;
    max-width: 960px;
    margin: 0 auto;
    padding-left: 10px;
    padding-right: 10px; }
    @media screen and (min-width: 768px) {
      .debugger > div {
        padding-left: 10px;
        padding-right: 10px; } }
    .debugger > div > div {
      height: inherit;
      display: flex;
      flex-wrap: wrap;
      margin-left: -10px;
      margin-right: -10px; }
      .debugger > div > div > div {
        box-sizing: border-box;
        word-wrap: break-word;
        margin-left: 10px;
        margin-right: 10px;
        width: calc(100% / 12 * 1 - 20px);
        height: inherit;
        outline: 1px solid rgba(255, 255, 0, 0.3);
        background: rgba(0, 0, 0, 0.08); }

